const uni = require('uni').default;
















































































































































import { mapState, mapGetters, mapMutations } from 'vuex'
import fotermenu from '../../components/menu.vue'	
import txtmessage from '../../components/message.vue'

// 导入公共方法
const PUBLIC = require('../../static/js/public.js')


export default {
	data(){
		return {
			isdown: true,
			iszan: false,
			cardId: '',
			mes: {
				
			},
			kefuurl: ``
		}
	},
	computed: {
		...mapState([
			'HTTPS_URL',
			'WSS_URL'
		])
	},
	components: {
		'fotermenu': fotermenu,
		'txtmessage': txtmessage
	},
	mounted(){
		
	},
	onLoad(query){
		let _this = this
		_this.bindMP(query.id).then((data)=>{
			// 上传用户行为 - 查看名片
			_this.UPDATA_USER_RADAR({
				type: 'visit_business_card',
				newdata: data.cardInfo
			})
		})
		
		
	},
	onShow(){
	
	},
	methods:{
		// 跳转到指定客服
		gokefu(userid){
			let _this = this
			// 上传用户行为 - 咨询
			_this.UPDATA_USER_RADAR({
				type: 'consult',
				newdata: _this.mes
			})
			// 跳转客服页面
			uni.navigateTo({
				url: `/pages/kefu/kefu?userid=${userid}`
			})
		},
		// VUEX - function
		...mapMutations([
			'UPDATA_USER_RADAR'
		]),
		// 绑定名片数据
		bindMP(id){
			let _this = this
			return new Promise((resolve, reject)=>{
				uni.request({
					url: `${this.HTTPS_URL}/businessCard/get?cardId=${id}`,
					method: 'GET',
					success:function(res){
						if(res.data.status === 1){
							_this.mes = res.data.data.cardInfo
							// 成功
							resolve(res.data.data)
						}
					}
				})
			})
		},
		// 展开
		down(){
			this.isdown = !this.isdown
		},
		// 点赞 
		zan(){
			this.iszan = !this.iszan
			if(this.iszan){
				// 上传用户行为 - 赞
				this.UPDATA_USER_RADAR({
					type: 'click_like_business_card',
					newdata: this.mes
				})
			}
		},
		// 拨打电话 
		call(nub){
			uni.makePhoneCall({
				phoneNumber: nub,
				success: ()=>{
					// 上传用户行为 - 拨打电话
					this.UPDATA_USER_RADAR({
						type: 'dial',
						newdata: this.mes
					})
				}
			})
		},
		// 复制
		copy(nub){
			uni.setClipboardData({
				data: nub,
				success:function(){
					uni.showToast({
						title: '复制成功'
					})
				}
			})
		},
		// 添加本地通讯录
		addphone(){
			uni.addPhoneContact({
				photoFilePath: '../../static/images/touxiang.jpg',
				nickName: this.mes.cardName,
				mobilePhoneNumber: this.mes.cardTel,
				weChatNumber: this.mes.cardWeixinid,
				organization: this.mes.companyName,
				title: this.mes.title,
				hostNumber: this.mes.cardTel,
				email: this.mes.cardEmail,
				success: (res)=>{
					uni.showToast({
						title: '保存成功',
						icon: 'success',
					})
					// 上传用户行为 - 保存通讯录
					this.UPDATA_USER_RADAR({
						type: 'save_to_address_book',
						newdata: this.mes
					})
				}
			})
		}
	},
	onShareAppMessage: function (res) {
		if (res.from === 'button') {
		  // 来自页面内转发按钮
		}
		
		
		// 上传用户行为 - 转发名片
		this.UPDATA_USER_RADAR({
			type: 'forward_business_card',
			newdata: this.mes
		})
		
		// 转发自定义内容
		return {
		  title: `${this.mes.cardName} - ${this.mes.title}`,
		  path: `/pages/getinfo/getinfo?cardId=${this.mes.id}`,
		  imageUrl: `${this.mes.cardBgImg}`
		}
		
	}
}



